.colors {
  color: #457b9d;
  color: #1d3557;
  color: #a8dadc;
  color: #f1faee;
  color: #e63946;
  color: #e0e0e0;
}

.list-budget-item {
  width: 85vw;
  height: 60px;
  margin: 2px 0;
  padding: 10px;

  display: flex;
  justify-content: flex-start;
  align-items: center;

  font-family: 'Roboto';
  box-shadow: 0px 5px 2px 1px #aaaaaa4f;
}

/****************************************************/
/* INCOME TYPE */

.list-budget-item-income {
  background-color: white;
  border-left: 5px solid #457b9d;
  transition: all 0.2s linear;
}

.list-budget-item-income-active {
  border: 5px solid #457b9d;
}

.list-budget-item-income span {
  margin: 2px;
  width: 60%;
  margin-right: 10px;
}

.list-budget-item-income span input,
textarea {
  margin: 2px;
  outline: none;
  border: 0;
  width: 100%;
  border: 2px solid #457b9d;
  transition: all 0.3s linear;
}

.list-budget-item-income span input {
  color: #457b9d;
  font-size: 18px;
  font-family: 'Oswald';
}

.list-budget-item-income span input:disabled {
  color: #457b9d;
  font-family: 'Oswald';
  background-color: transparent;
  border: 0;
}

.list-budget-item-income span textarea:disabled {
  resize: none;
  overflow: hidden;
  font-size: 16px;
  font-family: 'Roboto';
}

.list-budget-item-income > input {
  margin: 2px;
  outline: none;
  border: 0;
  text-align: left;
  width: 25%;

  color: #457b9d;
  font-size: 16px;
  font-family: 'Oswald';
  border: 2px solid #457b9d;
  transition: all 0.3s linear;
}

.list-budget-item-income > input:disabled {
  margin: 2px;
  background-color: transparent;
  outline: none;
  border: 0;
  text-align: center;
  width: 25%;

  color: #457b9d;
  font-family: 'Oswald';
}

