/****************************************************/

.list-budget-item-btn-options-container {
  position: relative;
}

.list-budget-item-btn {
  background-color: transparent;
  outline: none;
  border: 0;
  margin: 0 20px;

  display: flex;
  justify-content: center;
  align-items: center;

  color: #457b9d;
  font-size: 24px;
  font-family: 'Oswald';
  transition: all 0.1s linear;
}

.list-budget-item-btn-active {
  transform: rotate(180deg);
}

.list-budget-item-btn-options {
  position: absolute;
  background-color: #fff;
  border-radius: 5px;
  border: 2px solid #457b9d;
  top: 75%;
  left: -15%;
  width: 80px;
  z-index: 50;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  animation: MoveIn 0.1s linear;
}

.list-budget-item-btn-options button {
  background-color: transparent;
  outline: none;
  border: 0;
  margin: 5px;
  width: 100%;

  color: #457b9d;
  font-family: 'Oswald';
}

.list-budget-item-btn-options button:active {
  background-color: #457b9d;
  color: #fff;
}

.list-budget-item-btn-options div {
  width: 30%;
  height: 1px;
  background-color: #457b9d;
}

.list-budget-item-btn-options button > span {
  margin: 5px;
}

@keyframes MoveIn {
  0% {
    opacity: 0;
    transform: translateY(-10px);
  }
  100% {
    opacity: 1;
  }
}
